<template>
  <div class="left-content" :key="instanceKey">
    <div class="top-section section-box">
      <div class="section-bg">
        <SubtitleBar :subtitle="`发电统计`">
          <a-select
            v-model:value="formState.selectType"
            :options="selectTypeData"
            @change="handleTypeChange"
            style="width: 1.2rem; margin-right: 0.1rem"
          />
          <a-select
            v-model:value="formState.selectValue"
            :options="selectValueData"
            @change="handleTypeChange"
            style="width: 1.2rem"
          />
        </SubtitleBar>
        <div style="width: 100%; height: 1.85rem">
          <div style="margin-left: 0.16rem; transform: scale(0.85); transform-origin: 0 0">
            <EchartPillarChart
              :data="pChartData"
              :width="`26.06vw`"
              :height="tabStatus === 'on' ? `28.07vh` : `31.38vh`"
              :name="pChartName"
              :rotate="0"
              @click-item="powerToBiReport"
              :dataZoomDefaultValueRange="pChartData.dataZoomDefaultValueRange"
              :dataZoomDisabled="false"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="middel-section section-box">
      <div class="section-bg">
        <SubtitleBar :subtitle="`运维统计`">
          <a-select
            v-model:value="formState.operation"
            :options="operationTypeData"
            @change="handleOperationTypeChange"
            style="width: 1.2rem"
          />
        </SubtitleBar>
        <IndicatorGroup
          :style="tabStatus === 'on' ? 'margin-top: -0.06rem; ' : 'margin-top: -0.14rem'"
          class="indicator-group-layout"
          :class="tabStatus === 'on' ? '' : 'fix-indicator-group-padding'"
          :data="omData"
          @click-item="operationToBiReport"
        />
        <div style="width: 100%; height: 1.85rem">
          <!-- 
          <div style="transform: scale(0.65); margin-top: -0.55rem;">
            <TreeDPie v-if="NtgaleData?.pieData?.length > 0" 
              :data="NtgaleData.pieData" 
              :colors="NtgaleData.colors" 
              width="150%" :height="300" :showlegend="true"
            />
          </div> -->
          <div style="transform: scale(0.8); transform-origin: 0 0">
            <EchartPieChart
              id="pieChart"
              :width="`29.59vw`"
              :height="tabStatus === 'on' ? `25.2vh` : `28vh`"
              :data="NtgaleData.pieData"
              showLabel
              labelFormatter="{b}：{d}%"
              :roseType="false"
              :radius="['40%', '60%']"
              tooltipFormatter="{b}：{c}"
              @click-item="operationToBiReport"
            />
          </div>
        </div>
      </div>
    </div>

    <div class="bottom-section section-box">
      <div class="section-bg">
        <SubtitleBar :subtitle="`安全运行`">
          <a-select
            v-model:value="formState.safeOperation"
            :options="safeTypeData"
            @change="handleSafeTypeChange"
            style="width: 1.2rem"
          />
        </SubtitleBar>
        <IndicatorGroup
          :style="tabStatus === 'on' ? 'margin-top: -0.06rem' : 'margin-top: -0.14rem'"
          class="indicator-group-layout"
          :class="tabStatus === 'on' ? '' : 'fix-indicator-group-padding'"
          :data="securityData"
          @click-item="safetyToBiReport"
        />
        <div style="width: 100%; height: 1.85rem">
          <div
            style="width: 4rem; margin-top: 0.05rem; margin-left: 1.75rem; transform: scale(1.175)"
          >
            <WaterLevelChart
              :data="formState.signRage"
              :width="`1.5rem`"
              :height="`2rem`"
              :circle="{ top: '0.49rem', left: '0.24rem', width: '1.02rem', height: '1.02rem' }"
              @click-item="safetyToBiReport"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
  import { ref, reactive, UnwrapRef, onMounted, watch } from 'vue';
  import { assign } from 'min-dash';
  import SubtitleBar from '/@/components/Framework/Chart/SubtitleBar.vue';
  import IndicatorGroup from '/@/components/Framework/Chart/IndicatorGroup.vue';
  import EchartPillarChart from '/@/components/Framework/Chart/EchartPillarChart.vue';
  import WaterLevelChart from '/@/components/Framework/Chart/WaterLevelChart.vue';
  import EchartPieChart from '/@/components/Framework/Chart/EchartPieChart.vue';
  import { addTabPage } from '@/utils/route';
  import { handleRouteChange } from '../../utils';
  import * as OverviewApi from '@/api/da/cockpit/overview';
  import { buildUUID } from '@/utils/uuid';
  // import TreeDPie from '/@/components/Framework/Three/TreeDPie.vue';

  const props = defineProps({
    searchInfoByTime: { type: Object, default: {} },
    tabStatus: { type: String, default: '' },
  });

  //搜索参数
  const searchInfo: UnwrapRef<any> = reactive({});
  const instanceKey = ref(buildUUID());
  const formState = reactive({
    selectType: 'all',
    selectValue: 'online',
    planSelectType: 'all',
    operation: 'workOrder',
    safeOperation: 'responsibility',
    signRage: 0,
  });

  const marginTop = async (value) => {
    let marginTop = 0;
    if (tabStatus == 'on') {
      marginTop = '-0.18rem';
    }
    return marginTop;
  };

  // ==================================发电统计 ======================================
  const selectTypeData: any = ref([
    { label: '历年', value: 'all' },
    { label: '当年', value: 'inThoseYear' },
    { label: '近12个月', value: 'december' },
  ]);

  const selectValueData: any = ref([
    { label: '理论电量', value: 'theory' },
    { label: '实发电量', value: 'actual' },
    { label: '上网电量', value: 'online' },
    { label: '结算电量', value: 'settlement' },
  ]);

  const pChartName = ref(['电量']);
  const pChartData = ref({
    barData: [],
    categories: [],
    units: ['万kWh'],
    colors: [
      ['#488FF6', '#60D1F3'],
      ['#E59837', '#FAE895'],
      ['#078C5D', '#68E4B8'],
    ],
    dataZoomDefaultValueRange: [0, 5],
  });

  const doPowerStatByGen = async () => {
    const dayParams = {
      dateType: formState.selectType,
      index: formState.selectValue,
    };
    const powerStatByGen = await OverviewApi.powerStatByGen(dayParams);
    if (powerStatByGen) {
      pChartData.value.barData = powerStatByGen.barData || [];
      pChartData.value.categories = powerStatByGen.categories;
    }
    const selectValueObj = selectValueData.value.find(
      (item) => item.value === formState.selectValue,
    );
    if (selectValueObj) {
      pChartName.value.splice(0, pChartName.value.length);
      pChartName.value.push(selectValueObj.label);
    }
  };

  const handleTypeChange = () => {
    doPowerStatByGen();
  };

  // ==================================运维统计 begin ======================================
  const operationTypeData: any = ref([
    { label: '工单', value: 'workOrder' },
    { label: '巡检', value: 'inspection' },
    { label: '两票', value: 'twoVotes' },
  ]);
  const omData = ref({});
  const NtgaleData = ref({ pieData: [], colors: [] });

  const doWorkOrder = async (value) => {
    NtgaleData.value.pieData = [];
    NtgaleData.value.colors = [];
    if (value === 'workOrder') {
      const workOrder = await OverviewApi.workOrder({
        startMonth: searchInfo.startMonth,
        endMonth: searchInfo.endMonth,
      });
      if (workOrder) {
        omData.value = [
          { label: '工单总数', value: workOrder.total || 0 },
          { label: '未闭环工单数', value: workOrder.dealTotal || 0 },
          { label: '工单闭环率', value: workOrder.dealRate || 0 },
        ];
        if (workOrder.pieData) {
          const data = workOrder.pieData.map((item) => {
            return { name: item.name, value: item.value };
          });
          NtgaleData.value.pieData = [...data];
          NtgaleData.value.colors = ['#03AAFF90', '#62E99F90', '#00BBFF90', '#99DD0090'];
        }
      }
    } else if (value === 'inspection') {
      const inspection = await OverviewApi.inspection({
        startMonth: searchInfo.startMonth,
        endMonth: searchInfo.endMonth,
      });
      if (inspection) {
        omData.value = [
          { label: '未巡检数', value: inspection.unInspectionCount || 0 },
          { label: '巡检异常项', value: inspection.waitCount || 0 },
          { label: '巡检完成率', value: inspection.inspectionRate || 0 },
        ];
        if (inspection.pieData) {
          const data = inspection.pieData.map((item) => {
            return { name: item.name, value: item.value };
          });
          NtgaleData.value.pieData = [...data];
          NtgaleData.value.colors = ['#03AAFFA0', '#99DD00A0', '#62E99FA0', '#00BBFF90'];
        }
      }
    } else if (value === 'twoVotes') {
      const twoVotes = await OverviewApi.twoTicket({
        startMonth: searchInfo.startMonth,
        endMonth: searchInfo.endMonth,
      });
      if (twoVotes) {
        omData.value = [
          { label: '电气一票数', value: twoVotes.firstCount || 0 },
          { label: '电气二票数', value: twoVotes.secondCount || 0 },
          { label: '操作票数', value: twoVotes.operateCount || 0 },
        ];
        if (twoVotes.pieData) {
          const data = twoVotes.pieData.map((item) => {
            return { name: item.name, value: item.value };
          });
          NtgaleData.value.pieData = [...data];
          NtgaleData.value.colors = ['#03AAFF90', '#62E99F90', '#00BBFF90', '#99DD0090'];
        }
      }
    }
  };

  const handleOperationTypeChange = (value) => {
    doWorkOrder(value);
  };

  // ==================================安全运行 begin ======================================
  const safeTypeData: any = ref([
    { label: '安全责任书', value: 'responsibility' },
    { label: '安全检查', value: 'check' },
    { label: '安全工器具', value: 'tools' },
  ]);
  const securityData = ref({});

  const doSafeResp = async (value) => {
    if (value === 'responsibility') {
      const res = await OverviewApi.safeResp({
        startMonth: searchInfo.startMonth,
        endMonth: searchInfo.endMonth,
      });
      if (res) {
        securityData.value = [
          { label: '应签数', value: res.supposedSignNum || 0 },
          { label: '已签数', value: res.signNum || 0 },
          { label: '签订率(%)', value: res.signRage || 0 },
        ];
        formState.signRage = res.signRage;
      }
    } else if (value === 'check') {
      const res = await OverviewApi.safeCheck({
        startMonth: searchInfo.startMonth,
        endMonth: searchInfo.endMonth,
      });
      if (res) {
        securityData.value = [
          { label: '检查次数', value: res.checkCount || 0 },
          { label: '发现隐患(项)', value: res.hiddenCount || 0 },
          { label: '隐患处理率', value: res.dealRate || 0 },
        ];
        formState.signRage = res.dealRate;
      }
    } else if (value === 'tools') {
      const res = await OverviewApi.safeTools({
        startMonth: searchInfo.startMonth,
        endMonth: searchInfo.endMonth,
      });
      if (res) {
        securityData.value = [
          { value: res.withinNum || 0, label: '待检数(个)' },
          { value: res.unInspectedNum || 0, label: '超期未检数(个)' },
          { lvalue: res.qualifiedRate || 0, label: '合格率(%)' },
        ];
        formState.signRage = res.qualifiedRate;
      }
    }
  };
  const handleSafeTypeChange = (value) => {
    doSafeResp(value);
  };

  // 更新组件函数
  const handleUpdate = () => {
    instanceKey.value = buildUUID();
  };

  watch(
    () => props.searchInfoByTime,
    (value) => {
      assign(searchInfo, value);
      doWorkOrder(formState.operation);
      doSafeResp(formState.safeOperation);
    },
  );

  //==================================穿透方法======================================
  const powerToBiReport = (params) => {
    addTabPage('/bi/elec/powerContrast', '电量对比统计', {
      dateType: 'month',
      startDate: formState.selectType === 'all' ? params.name + '-01' : params.name,
      endDate: formState.selectType === 'all' ? params.name + '-12' : params.name,
    });
  };

  const operationToBiReport = () => {
    if (formState.operation === 'twoVotes') {
      //两票
      handleRouteChange('/bi/maintenance/ticket', {});
    } else if (formState.operation === 'workOrder') {
      //工单
      handleRouteChange('/bi/maintenance/workorder', {});
    } else if (formState.operation === 'inspection') {
      //巡检
      handleRouteChange('/bi/inspection/index', {});
    }
  };

  const safetyToBiReport = () => {
    if (formState.safeOperation === 'responsibility') {
      //安全责任书
      addTabPage('/bi/safety/agreement');
    } else if (formState.safeOperation === 'check') {
      //安全检查
      handleRouteChange('/bi/safety/check', {
        dateCycle: 'month',
      });
    } else if (formState.safeOperation === 'tools') {
      //安全工器具
      addTabPage('/bi/safetytools/index');
    }
  };

  defineExpose({
    handleUpdate,
  });

  onMounted(async () => {
    doPowerStatByGen();
  });
</script>

<style lang="less" scoped>
  .left-content {
    display: flex;
    position: absolute;
    z-index: 99;
    top: 0.9rem;
    left: 0;
    flex-direction: column;
    width: 25vw;
    height: calc(100vh - 0.9rem);
    padding: 0 0.57vw 1.68vh 0.68vw;

    .section-box {
      flex: 1;
      width: 100%;
      height: 33.3333%;
      overflow: hidden;

      .section-bg {
        width: 100%;
        height: 100%;
        background: url('../../../../assets/images/da/section_bg_1.png') no-repeat center/100% 100%;
      }
    }

    .top-section {
      padding-bottom: 0.62vh;
    }

    .middel-section {
      padding: 0.31vh 0;
    }

    .bottom-section {
      padding-top: 0.62vh;
    }

    .indicator-group-layout {
      padding: 0 13.26%;

      &.fix-indicator-group-padding {
        padding-top: 1.2vh;
      }
    }
  }
</style>
